.form-field {
   $parent: &;
   position: relative;
   &__input {
      width: 100%;
      height: 60px;
      padding: 0 20px 0 54px;
      @include f('ub400', 14, 24);
      border-radius: 30px;
      background-color: $white;
      border: 1px solid #e6e6e6;
      transition: .3s ease-in-out;
      @include placeholder-color($color5);
      color: $color2;
      @include xs {
         padding: 0 20px 0 37px;
         height: 50px; }
      &:focus {
         border-color: #cf0110;
         ~ #{$parent}__icon svg {
             fill:  #cf0110; } }
      //#{$parent}.has-error &
 }      // border-color: #cf0110

   &__icon {
      position: absolute;
      top: 0;
      left: 0;
      width: 40px;
      height: 60px;
      color: #3198f0;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-size: 10px;
      @include xs {
         width: 30px;
         height: 50px; }
      svg {
         position: relative;
         top: 1px;
         transition: .3s ease-in-out;
         fill: $color5; }
      #{$parent}_message & {
         bottom: auto;
         height: 60px; }
      //#{$parent}.has-error &
 }      // color: rgba(213, 29, 72, 1)
   &__choise {
      position: absolute;
      width: 162px;
      bottom: -26px;
      left: calc(50% - 81px);
      text-align: center;
      font-size: 14px;
      color: $color2;
      @include xs {
         position: relative;
         left: auto;
         bottom: auto;
         margin: 0 auto -15px; }
      &::before, &::after {
         position: absolute;
         width: 47px;
         top: 50%;
         height: 1px;
         background-color: #000000;
         opacity: 0.06;
         content: ''; }
      &::before {
         left: 0; }
      &::after {
         right: 0; } } }

.textarea {
   $parent: &;
   display: block;
   width: 100%;
   height: 150px;
   padding: 18px 20px 18px 55px;
   resize: none;
   background-color: $white;
   border-radius: 30px;
   border: 1px solid #e6e6e6;
   @include f('ub400', 14, 24);
   transition: .3s ease-in-out;
   @include placeholder-color($color5);
   color: $color2;
   @include xs {
      padding: 18px 20px 18px 37px; }
   &:focus {
      border-color: #cf0110;
      ~ .form-field__icon svg {
         fill:  #cf0110; } } }

.form-submit-btn {
   cursor: pointer;
   input {
      display: none; } }

.check-box {
   $parent: &;
   &.is-disabled {
      pointer-events: none; }
   &__label {
      display: inline-flex;
      align-items: flex-start;
      cursor: pointer; }

   &__square {
      position: relative;
      width: 12px;
      height: 12px;
      flex: 0 0 12px;
      margin-top: 7px;
      border-radius: 4px;
      border: 1px solid #808080;
      @include xs {
         margin-top: 5px; }
      #{$parent}_type-1 & {
         margin-right: 20px; }
      #{$parent}_type-2 & {
         margin-right: 10px; }
      #{$parent}.is-disabled & {
         opacity: .3; }
      #{$parent}_radio & {
         &::after {
            position: absolute;
            top: calc(50% - 3px);
            left: calc(50% - 3px);
            opacity: 0;
            transform: scale(0);
            width: 6px;
            height: 6px;
            background-color: $color1;
            border-radius: 50%;
            transition: .3s ease-in-out;
            content: ''; } } }
   &__icon {
      opacity: 0;
      transform: scale(0);
      position: absolute;
      top: -1px;
      left: 2px;
      transition: .3s ease-in-out;
      font-size: 10px;
      fill: $color1;
      #{$parent}_radio & {
         display: none; } }
   &__text {
      @include f('ub400', 14, 24);
      @include xs {
         line-height: 20px; }

      #{$parent}_type-2 & {
         color: $color5; } }
   &__input {
      display: none;
      &:checked ~ #{$parent}__square #{$parent}__icon,
      &:checked ~ #{$parent}__square::after {
         opacity: 1;
         transform: scale(1); } } }


// *** begin contact-form *** //
.contact-form {
   $parent: &;
   position: relative;
   padding: 35px 40px 45px;
   box-shadow: 0 15px 40px rgba(26, 3, 4, 0.1);
   background-color: $white;
   text-align: center;
   @include xs {
      padding: 35px 20px 45px; }
   &_dots-1 {
      &::after {
         pointer-events: none;
         position: absolute;
         top: 0;
         right: 0;
         width: 356px;
         height: 369px;
         background-image: url("../img/decor/decor-dots_5.png");
         background-repeat: no-repeat;
         content: ''; } }
   &_dots-2 {
      &::after {
         pointer-events: none;
         position: absolute;
         bottom: 9px;
         right: 0;
         width: 370px;
         height: 382px;
         background-image: url("../img/decor/decor-dots_6.png");
         background-repeat: no-repeat;
         content: ''; } }
   &_complex {
      &::after {
         bottom: 40px;
         right: -20px; } }
   &_complex .contact-form__field_email {
      @include xs {
         order: -1;
         margin: 0 0 15px!important; } }

   &_column {
      padding: 35px 55px 45px; }

   &__title {
      position: relative;
      margin-bottom: 20px;
      padding-bottom: 10px;
      @include f('ub700', 26, 34);
      color: $color2;
      &::before {
         position: absolute;
         left: calc(50% - 2.5px);
         bottom: -2px;
         z-index: 1;
         width: 5px;
         height: 5px;
         background-color: $color1;
         border-radius: 50%;
         content: ''; }
      &::after {
         position: absolute;
         bottom: 0;
         left: calc(50% - 35px);
         width: 70px;
         height: 1px;
         background-color: $color1;
         opacity: 0.1;
         content: ''; }
      span {
         @include f('ub300'); } }
   &__row {
      @include xs {
         flex-direction: column;
         margin: 0; }
      #{$parent}_complex & {
         margin: 0 -15px;
         @include xs {
            margin: 0; } }
      #{$parent}_contacts & {
         margin: 0 -10px;
         @include xs {
            margin: 0; } }

      &:not(:last-child) {
         margin-bottom: 30px;
         @include xs {
            margin-bottom: 15px; } } }
   &__field {
      position: relative;
      z-index: 1;
      flex: 1 1 calc(50% - 30px);
      width: calc(50% - 30px);
      margin: 0 15px;
      @include xs {
         flex: 0 1 auto;
         width: 100%;
         margin: 0; }
      #{$parent}_column & {
         flex: 0 1 auto;
         width: 100%;
         margin: 0;
         &:not(:last-child) {
            margin-bottom: 30px;
            @include xs {
               margin-bottom: 15px; } } }
      #{$parent}_complex & {
         margin: 0 15px;
         @include xs {
            margin: 0; } }
      #{$parent}_contacts & {
         margin: 0 10px;
         @include xs {
            margin: 0; } }
      &:not(:last-child) {
         @include xs {
            margin-bottom: 15px; } }
      &.form-field_message {
         .is-ie & {
            flex: 1 1 calc(100% - 30px);
            width: calc(100% - 30px); } } }
   &__submit-btn {
      position: relative;
      z-index: 1;
      min-width: 280px;
      @include xs {
         min-width: 0;
         width: 100%; }
      #{$parent}_contacts & {
         flex: 1 1 calc(50% - 20px);
         width: calc(50% - 20px);
         min-width: 240px; } }
   &__dots {
      @include coverdiv;
      pointer-events: none;
      &::before, &::after {
         position: absolute;
         top: 15px;
         width: 4px;
         height: 4px;
         border-radius: 50%;
         background-color: $color1;
         content: ''; }
      &::before {
         left: 15px; }
      &::after {
         right: 15px; }
      &-el {
         &::before, &::after {
            position: absolute;
            bottom: 15px;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: $color1;
            content: ''; }
         &::before {
            left: 15px; }
         &::after {
            right: 15px; } } } }

// end contact-form //
